        h2 2.1 Tipos de perfiles

      p(data-aos="fade-down").mb-5 Los tipos de perfiles del <i>Buyer</i> Persona pueden ser agrupados segn sea su participacin en el proceso de una compra. Los siguientes tres perfiles se destacan por sus diferentes roles y comportamientos as:

    .row.justify-content-center.align-items-stretch.mb-3          
