            p(data-aos="fade-down").mb-0 Es el prototipo de individuo con el que una marca desea conectarse y quien es el responsable de tomar la decisin de compra. De forma habitual, las marcas suelen poseer un nico <i>Buyer</i> Persona Principal, pero, es posible que existan casos donde se diseen varios prototipos, lo que depender del contexto empresarial.   
      .col-lg-4.my-3
        .bg16.p-4.h-100
