            p(data-aos="fade-down") Influye de alguna manera sobre el <i>Buyer</i> Persona principal asi:

            p(data-aos="fade-down") #[b Prescriptores] tienen autoridad y que sugieren el producto, su opinin es concluyente y, por lo general, no se discute.

            p(data-aos="fade-down") #[b Influencers] y los consejeros pueden tener un efecto tanto positivo como negativo sobre una decisin en la compra de un producto, porque su opinin depender de la imagen percibida por cada persona o cliente. 

            p(data-aos="fade-down") #[b Recomendadores] que se limitan a recomendar el producto, pero no poseen la capacidad de influencia de las anteriores figuras.    
