            p(data-aos="fade-down").mb-0 Prototipo de individuo que no encarnan a los clientes inmediatos de los productos actuales de la marca, pero que en algn momento se pueden sentir atrados por los contenidos o campaas publicitarias. Tambin se puede tratar de prototipos de individuos que s compran el producto, pero, no lo vuelven a hacer (porque no los satisfizo, porque no tuvieron una buena asesora o servicio) y, por lo tanto, no entregan rentabilidad a la empresa. Se les puede conocer como "anti-<i>Buyer</i> Persona".

