        p(data-aos="fade-down") Segn Vsquez (2021), la famosa frase "T eres lo ms importante" ha sido usada hasta el cansancio por la publicidad tradicional, pero, en la actualidad, no basta con decirlo, hay que demostrarlo. Si as fuera los clientes viviran experiencias de marca ms significativas y con un alto ndice de personalizacin. Empresas como Amazon, Disney y Spotify, consiguieron personalizar la CX con bastante xito, debido al diseo de arquetipos, pero la realidad es que existen millones de empresas en las que este tema est pendiente.

