      .col-lg-8
        p(data-aos="fade-down") Segn Beltrn, Ledesma, y Parrales (2019) la estrategia de venta falla estrepitosamente cuando las empresas no definen su <i>Buyer</i> Persona. Es por esto que su definicin puede lograr la diferencia entre una estrategia positiva y una fracasada. Uno de los errores del mercadeo digital consiste en pensar que el mensaje va a cautivar a todos los clientes, cuando realmente la efectividad se logra conociendo perfectamente los prospectos reales. 
        .row.justify-content-start.align-items-center    
          .col-lg-10.col-md-12.up.col-sm-12
            p(data-aos="fade-down").mb-0 El anlisis realizado sobre el <i>Buyer</i> Persona admite comprender a quin se le habla y cules son los mtodos precisos y oportunos para conquistarlos. En un mundo digital repleto de informacin (mucha de ella considerada <i>fake news</i>) es vital comprender cmo captar la atencin de los prospectos, cmo hacer que elijan la marca y no la competencia.

        .tarjeta.position-relative(data-aos="flip-down")
          .imagen-absolute
