    p(data-aos="fade-down") Hay que establecer perfectamente cules son los prospectos a los que se desea cautivar, si se piensa en el embudo de ventas que precisa de la atraccin de extraos, llevndolos a visitar un sitio #[i web o landing page], para convertirlos, inicialmente en prospectos, y a futuro en clientes actuales. Conquistar al prospecto perfecto es el comienzo del xito de toda campaa de mercadeo digital,  es por esta razn que se utiliza el anlisis de <i>Buyer</i> Persona, que permite hallar a esos extraos para conquistarlos, usando tcnicas de conversin. En mercadeo digital se trabaja para la atraccin, la conversin y luego conquista del prospecto.

