      .col-lg-4.my-3      
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img154.png', alt='')
      .col-lg-8.my-3
        p(data-aos="fade-down") Las respuestas van a permitir la compresin sobre cmo hacer la bsqueda y la atraccin de los prospectos perfectos para nutrir el embudo de ventas. Existen distintas estrategias para responder estas preguntas y normalmente se usan plataformas de analtica, de investigacin de palabras claves y de escucha social. Pero, lo ms importante para comenzar es la definicin de quin es el cliente, lo que no se soluciona con una plataforma tecnolgica sino con un proceso de lluvia de ideas que se llama anlisis del <i>buying</i> Persona que ayuda a representar y a modelar al potencial cliente.
        .row.justify-content-start.align-items-center.bg18.p-1    
          .col-lg-10.col-md-12.up.col-sm-12
            p(data-aos="fade-down").mb-0.px-4 Una vez creado el <i>Buyer</i> Persona, se puede continuar con una estrategia que integre campaa publicitaria, cimentacin del sitio web y contenidos digitales. En mercadeo digital, estas tres labores deben dar tiene que ser espectaculares para que se puedan lograr los objetivos pactados.
          .col-lg-2.col-md-10.d-none.d-lg-block  
            img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img155.svg', alt='') 

    p(data-aos="fade-down").mb-5 Pero, hay que entender que, si no se le habla a la persona correcta, los prospectos que visiten el sitio web nunca llegarn a ser clientes actuales. Por esta razn, el equipo de mercadeo debe tener muy claro quin es el <i>Buyer</i> Persona para orientar los esfuerzos de publicidad, sitio web y gestin de contenidos digitales de la forma ms oportuna posible.

    .row.justify-content-center.align-items-center.mb-2           
