      .col-lg-10.my-3
        p(data-aos="fade-down") Ahora bien, existen herramientas gratuitas de analtica, como, por ejemplo, #[b #[i Google Data Studio o Google Anatytics]], con las que se puede entender el perfil de los clientes reales que visitan y compran en el sitio web para luego crear el <i>Buyer</i> Persona. De esta manera su creacin est basada en datos reales. 

    p(data-aos="fade-down").mb-5 Con estas herramientas se pueden observar puntos clave relativos a la audiencia segn datos como: 
