    p(data-aos="fade-down").mb-5 Existen diferentes KPI de CX, pero, algunos son ms importantes que otros porque brindan datos valiosos para las marcas. Son mtrica llamadas <i>Customer Service</i> que toda empresa debera considerar en su estrategia de anlisis y medicin:

    .tarjeta.tarjeta--gris.p-5.mb-5(data-aos="fade-left")
      SlyderA(tipo="b")
        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 <i>Net Promoter Score (NPS).</i>
            p Se trata de uno de los CX KPI ms relevantes, porque mide la probabilidad que tiene un cliente de recomendar una marca o sus productos a sus contactos. Se centra en la intencin del cliente y no en una sensacin o algo emocional (que puede variar).

            p.mb-4 El NPS se efecta a travs de una encuesta, en la que las respuestas obtenidas se recopilan en una escala de 1 al 10.              
 
            .bg20.p-4
              h5.mb-4 Ejemplo:
              p Qu tan factible es que usted recomiende nuestra marca? 
              p.mb-0 Siendo 10 las ms factible y 1 la menos factible. 
        
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img169.png', alt='')  

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 <i>Customer Effort Score (CES).</i>  
            p.mb-4 Es el #[b Indicador de Esfuerzo del Cliente], es decir, el nivel de esfuerzo del cliente para tener una relacin con la empresa. Con este indicador, los clientes califican su CX en relacin al uso de los productos de una marca eligiendo variables ubicadas entre muy difcil a muy fcil. A travs de esta asignacin, la marca puede establecer cul es el esfuerzo que requiri el cliente para comprar el producto y la probabilidad de que lo siga haciendo. 
            .bg20.p-4
              h5.mb-4 Ejemplo:
              p Cunto esfuerzo tuviste que hacer para utilizar nuestro servicio?   
              p.mb-0 Extremadamente difcil; muy difcil; difcil; aceptable; media; fcil; extremadamente fcil.
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img170.png', alt='') 

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Satisfaccin del cliente (CSAT). 
            p.mb-4 El #[b #[i Customer Satisfaction Scores]], mide el nivel de satisfaccin que tiene el cliente con el producto. Usualmente es usado para valorar interacciones especficas como puede la atencin al cliente va telefnica o el proceso de devolucin de un producto. En esta mtrica se pide al cliente calificar su nivel de satisfaccin en una escala que va de #[b Totalmente satisfecho] a #[b Totalmente insatisfecho]. 
            .bg20.p-4
              h5.mb-4 Ejemplo:
              p Qu tan satisfecho ests con el servicio que acabas de recibir?
 
              p.mb-0 Totalmente satisfecho; satisfecho; indiferente; no muy satisfecho; totalmente insatisfecho. 
        
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img171.png', alt='')

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Valor de Vida del Cliente (CLV). 
            p.mb-4 El #[b #[i Customer Lifetime Value]] es otro importantsimo KPI de CX que todo negocio debe considerar porque se refiere al valor de un cliente para la marca durante su vida, o sea, el tiempo en el que es cliente actual. Con este indicador se puede saber la rentabilidad que el cliente ha dejado a la marca a lo largo del tiempo y determinar los ingresos totales que se pueden conseguir de l. 
            .bg20.p-4
              h5.mb-4 Ejemplo:
              p Un atleta compra tenis 4 veces al ao durante 8 aos en la misma empresa de artculos deportivos, lo que puede valer:

              p.mb-0 $100 US por par de tenis, multiplicado por 4 pares al ao, durante 8 aos= 100 x 4 x 8 = $3.200 US 
        
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img172.png', alt='')

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Retencin de clientes. 
            p Es ms econmico para una empresa retener los clientes actuales que conseguir clientes nuevos. Por eso, es vital estar pendientes de las mtricas para calcular el nivel de fidelidad de los clientes y su tasa de retencin. Este indicador mostrar el porcentaje de clientes que continan con la marca luego de un periodo de tiempo. Poseer una tasa alta de retencin da a entender que los clientes estn satisfechos.

            p #[b Frmula para calcular la retencin de clientes:] [nmero de clientes al final del perodo menos el nmero de clientes nuevos adquiridos durante el perodo] dividido [nmero de clientes al comienzo del periodo] por 100. 
        
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img173.png', alt='')

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Satisfaccin de los empleados. 
            p No se puede olvidar la experiencia y satisfaccin de los empleados. Si ellos no estn contentos, sera bastante difcil que la empresa pudiera brindar una buena CX. Por eso, para lograr este indicador, se debe preguntar directamente a cada empleado qu tan bien se siente trabajando en la empresa, si estn orgullosos de pertenecer a su equipo de trabajo o qu mejoraran. En esta mtrica se pide al empleado calificar su nivel de satisfaccin laborando en la empresa en una escala que va de Totalmente satisfecho a Totalmente insatisfecho.
            .bg20.p-4
              h5.mb-4
              p Qu tan bien te sientes haciendo tu trabajo?

              p.mb-0 Totalmente satisfecho; satisfecho; indiferente; no muy satisfecho; totalmente insatisfecho. 
        
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img174.png', alt='')

    .row.justify-content-center.align-items-center.mb-2           
      .col-lg-2.my-3 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/187.png', alt='')   
      .col-lg-10.my-3
        p(data-aos="fade-down") Como se puede observar, los KPI de CX son vitales para que una empresa crezca. Si no se tiene una idea clara de cmo estn funcionando las estrategias, sus planes de accin y, en general, la empresa, de nada va a servir la implementacin de nuevas tecnologas para la atencin al cliente, para mejorar los servicios o para el diseo de una excelente estrategia de <i>marketing</i>. La aplicacin de estos indicadores ayudar a la empresa a conocer si va en el camino correcto o si debe desarrollar un plan de mejoramiento. (Pozo, 2018). 

    .row.justify-content-start.align-items-center.mb-5           
      .col-auto
        .list.bg2(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/v.svg', alt='')
          .list__txt
            h3.mb-0.mt-1.pe-3 Acciones estratgicas de mejora de la experiencia del cliente
            
    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-4      
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img175.png', alt='')
      .col-lg-8
        p(data-aos="fade-down") En la actualidad, brindar una excelente CX es vital para que la marca conserve la satisfaccin del cliente Castaeda (2022). Por lo tanto, es posible incrementar la retencin y la fidelidad, mejorando los resultados. Por eso, se necesita entender que la CX est estrechamente relacionada con los nuevos hbitos de conducta del cliente. Entonces, los expertos deben establecer mejores estrategias de servicio y relaciones comerciales para la satisfaccin del cliente. A continuacin, se pueden observar diez (10) acciones de mejora que las marcas pueden elegir para brindar una significativa CX.
        
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg13")
      .row(titulo="Mapear el pblico")
        .col-md-10.mb-4.mb-md-0
          p Saber quin es el cliente y mapear su perfil es vital para conceptualizar estrategias que tengan ms relacin con las expectativas del cliente y que satisfagan mejor las particularidades que se han identificado. 
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img176.svg', alt='') 
      .row(titulo="Escuchar a los clientes")
        .col-md-10.mb-4.mb-md-0
          p La ejecucin de encuestas de satisfaccin del cliente logra que los especialistas conozcan la imagen que el cliente tiene sobre la calidad en la atencin. Adems, identifica las recomendaciones y diatribas que podran ser usadas para una mejora en los procesos. 
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img177.svg', alt='') 
      .row(titulo="Personalizar la experiencia")
        .col-md-10.mb-4.mb-md-0
          p En la actualidad, ste es un tema vital a la hora de satisfacer al cliente. Por lo tanto, se deben descubrir las predilecciones de los clientes para incorporar estos conocimientos en las estrategias y actividades diseadas. Esto ayudar a que la marca sea ms asertiva en los puntos de contacto con el cliente y en su relacin con l. 
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img178.svg', alt='') 
      .row(titulo="Calificar el equipo")
        .col-md-10.mb-4.mb-md-0
          p Un buen equipo de trabajo es decisivo en la experiencia que se pueda brindar en el servicio y asesora al cliente. Por eso, se debe asegurar de los miembros tengan el manejo completo de: 
          .list.mb-3
            .list__item
              .list__item__icon
                img(src='@/assets/curso/temas/v3.svg', alt='')
            .list__txt
              p.mb-0 Tecnologas usadas.

          .list.mb-3
            .list__item
              .list__item__icon
                img(src='@/assets/curso/temas/v4.svg', alt='')
            .list__txt
              p.mb-0 Instrucciones para la resolucin de demandas.

          .list.mb-3
            .list__item
              .list__item__icon
                img(src='@/assets/curso/temas/v5.svg', alt='')
            .list__txt
              p.mb-0 Actitud apropiada en el trabajo, con empata y transparencia.

          .list.mb-3
            .list__item
              .list__item__icon
                img(src='@/assets/curso/temas/v6.svg', alt='')
            .list__txt
              p.mb-0 Buen contacto e interaccin con el cliente.            
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img179.svg', alt='') 
      .row(titulo="Invierte en tecnologa")
        .col-md-10.mb-4.mb-md-0
          p El uso de nuevas tecnologas ayuda a mejorar las relaciones con el cliente. Por eso, con esta inversin se hace posible la optimizacin en el funcionamiento de la empresa y la mejora en la resolucin de demandas, adems de poseer particularidades que le admiten mejorar CX y el servicio al cliente. Por lo tanto, se sugiere invertir en tecnologa, en especial, si la marca est atendiendo Millennials, Centennials o individuos de la generacin Z, porque son muy exigentes en relacin con la eficiencia y la rapidez de las interacciones. 
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img180.svg', alt='') 
      .row(titulo="Optimizar los procesos")
        .col-md-10.mb-4.mb-md-0
          p El mantenimiento ajustado de los procesos permite ser ms veloz y tener ms claridad a la hora de lograr la resolucin de las demandas. De igual forma, suministra una disminucin en los cuellos de botella operativos que complican a la empresa para tener equipos de trabajo productivos y satisfacer a los clientes. Por otro lado, tambin ayuda a disminuir los costos operativos. 
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img181.svg', alt='') 
      .row(titulo="Ser transparente")
        .col-md-10.mb-4.mb-md-0
          p Actualmente, los clientes se encuentran cada vez ms comprometidos con las marcas (Engagement  compromiso de marca), por lo que desean tener una relacin transparente y honesta. Por este motivo, es vital poseer polticas ntidas y cumplirlas, adems de tener el servicio oportuno. Si en algn momento, un empleado no sabe cmo solucionar un problema mostrado por el cliente, le debe informar que buscar una resolucin y que lo contactar ms tarde, cuando tenga la solucin a la mano. 
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img182.svg', alt='') 
      .row(titulo="Hacer el postventa")
        .col-md-10.mb-4.mb-md-0
          p Los clientes de hoy se interesan por tener relaciones duraderas con las marcas afines. Por eso, valoran mucho el servicio postventa y el afianzamiento de la relacin. Entonces, la empresa debe certificar una excelente CX antes del cierre, asegurndose de que todo funciona bien. Para identificarlo, se puede solicitar una evaluacin, preguntando al cliente si la entrega estuvo bien y ponindose a su disposicin. 
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img183.svg', alt='') 
      .row(titulo="Monitorear resultados")
        .col-md-10.mb-4.mb-md-0
          p Hacer un seguimiento a la relacin con el cliente y los resultados de su servicio, es una manera de comprobar si la marca cumpli con las expectativas y ha entregado una CX inolvidable. Como ejemplo, si la tasa de retencin de clientes disminuye, es porque algo no funciona bien en los procesos y los especialistas deben hacer una revisin. 
        .col-md-2
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img184.svg', alt='') 
      .row(titulo="Adoptar una plataforma omnicanal")
        .col-md-10.mb-4.mb-md-0
